<template>
  <v-app>
    <!-- <v-app-bar
      app
      color="primary"
      dark
    >
    </v-app-bar> -->

	<!-- <v-list dense>
	<v-subheader>LIST COMPONENTS:</v-subheader>
		<v-list-item
			v-for="(item, i) in items"
			:key="i"
			:to="item.to"
		>
			<v-list-item-icon>
				<v-icon>{{ item.icon }}</v-icon>
			</v-list-item-icon>
			<v-list-item-content>
				<v-list-item-title>{{ item.text }}</v-list-item-title>
			</v-list-item-content>
		</v-list-item>
	</v-list> -->

    <div class="d-flex" style="height: 100%;">
		<center>
			<v-card class="pt-10 pb-10" width="50" height="100%" style="background-color: #e6e6e6;">
				<!-- drawer = !drawer -->
				<v-icon v-if="icon == 'mdi-menu'"  @click="drawer = true, trial(), playAnimation()" style="text-align: center;">
					{{this.icon}}
				</v-icon>
				<v-icon v-else-if="icon == 'mdi-menu-open'" class="menuOpen"  @click="drawer = false, trial(), playAnimationReset()" style="text-align: center;">
					{{this.icon}}
				</v-icon>

				<!-- <hr style="height: 85%; transform: rotate(-90deg);"> -->
				
				<div style="height: 85%;"></div>
				<p style="transform: rotate(-90deg); margin-bottom: 8px; font-family: 'Young Serif', serif;">2023</p>
				<p style="transform: rotate(-90deg); font-family: 'Young Serif', serif;">
					<v-icon>mdi-copyright</v-icon>
				</p>
			</v-card>

		</center>

	  	<v-card :height="this.screenHeight" width="100%" class="py-10 pr-10" elevation="0" style="background-color: #e6e6e6;">
			<v-card id="particles-js" height="100%" width="100%" :style="{ backgroundImage: 'url(' + imageUrl + ')' }" style="border: 1px solid gray; background-color: #e6e6e6;" elevation="0" outlined tile>
				<!-- <v-card style="overflow: hidden;"> -->
				<div v-show="drawer == true" style="position: absolute; display: flex; top: 0; right: 0;"> 

					<div style="display: block; width: 350px;">
						<div v-for="(item, i) in items"
						:key="i"
						>
							<router-link style="text-decoration: none; text-align: start;" :to="item.to">
								<h1 v-if="item.to != currentRoute" @click="checkRouterPath(item.to)" :class="{'fade-in': itemAnimated[i]}" class="categories"  v-show="itemAnimated[i]">{{item.text}}</h1>
								<h1 v-else-if="item.to == currentRoute" :class="{'fade-in': itemAnimated[i]}" class="categories" v-show="itemAnimated[i]" style="text-decoration: line-through;">{{ currentRoute }}</h1>
							</router-link>
							
						</div>
					</div>
				</div>
					
				<!-- </v-card> -->
				<v-main style="position: absolute;">
					<router-view/>
				</v-main>
				
			</v-card>
		</v-card>
    </div>
  </v-app>
</template>

<script>
import { EventBus } from './eventBus.js';
import 'particles.js';
export default {
  name: 'App',

  	data: () => ({
		screenHeight: '',
		screenWidth: '',

		icon: 'mdi-menu-open',
		drawer: true,
		items:[
			{text:'Home', to:'home'},
			{text:'Projects', to:'projects'},
			{text:'About', to:'about'},
			// {text:'Contact', to:'contact'}
		],
		itemAnimated: [],
		imageUrl: "",
		currentRoute: ''
	}),

	methods: {

		checkRouterPath(route){
			console.log(route)
			this.currentRoute = route;

			if(this.currentRoute == 'home'){
				this.imageUrl = 'https://decorsave.co.uk/media/catalog/product/cache/065dc27cee58133323abcb0eca74cc7c/h/o/holden-decor-glasshouse-manyara-navy-teal-wallpaper-90152-2.2811.jpg';
			}else{
				this.imageUrl = '';
			}
			
		},

		async playAnimation() {
			for (let i = 0; i < this.items.length; i++) {
				if(i == 0){
					this.$set(this.itemAnimated, i, true); // Enable animation for this item
				}else{
					await this.delay(500); // Adjust delay as needed (in milliseconds)
					this.$set(this.itemAnimated, i, true); // Enable animation for this item
				}	
			}

			// Reset the animations after a delay
			await this.delay(1200); // Adjust the reset delay as needed
			// this.itemAnimated = this.items.map(() => false);
		},

		delay(ms) {
			return new Promise(resolve => setTimeout(resolve, ms));
		},

		playAnimationReset(){
			this.itemAnimated = this.items.map(() => false);
		},

		trial(){
			if(this.icon == 'mdi-menu-open'){
				this.icon = 'mdi-menu'
			}else{
				this.icon = 'mdi-menu-open'
			}
		
		},

		initParticles() {
			window.particlesJS("particles-js", {
				"particles": {
					"number": {
						"value": 80,
						"density": {
							"enable": true,
							"value_area": 700
						}
					},
					"color": {
						"value": "#030813"
					},
					"shape": {
						"type": "triangle",
						"stroke": {
							"width": 0,
							"color": "#030813"
						},
						"polygon": {
							"nb_sides": 5
						},
					},
					"opacity": {
						"value": 0.5,
						"random": false,
						"anim": {
							"enable": false,
							"speed": 1,
							"opacity_min": 0.1,
							"sync": false
						}
					},
					"size": {
						"value": 3,
						"random": true,
						"anim": {
							"enable": true,
							"speed": 1,
							"size_min": 10,
							"sync": false
						}
					},
					"line_linked": {
						"enable": true,
						"distance": 150,
						"color": "#030813",
						"opacity": 0.4,
						"width": 1
					},
					"move": {
						"enable": true,
						"speed": 0.50,
						"direction": "none",
						"random": false,
						"straight": false,
						"out_mode": "in",
						"bounce": false,
						"attract": {
							"enable": false,
							"rotateX": 600,
							"rotateY": 1200
						}
					}
				},
				"interactivity": {
					"detect_on": "canvas",
					"events": {
						"onhover": {
								"enable": true,
								"mode": "grab"
						},
						"onclick": {
							"enable": true,
							"mode": "push"
						},
						"resize": true
					},
					"modes": {
						"grab": {
							"distance": 140,
							"line_linked": {
								"opacity": 1
							}
						},
						"bubble": {
							"distance": 400,
							"size": 40,
							"duration": 2,
							"opacity": 8,
							"speed": 3
						},
						"repulse": {
							"distance": 200,
							"duration": 0.4
						},
						"push": {
							"particles_nb": 4
						},
						"remove": {
							"particles_nb": 2
						}
					}
				},
				"retina_detect": true
			})
		},

		getScreenHeightWidth(){
			console.log(window.innerHeight, '|', window.innerWidth)
			this.screenHeight = window.innerHeight;
			this.screenWidth = window.innerWidth;
		},
	},

	mounted() {
		this.initParticles();
		this.getScreenHeightWidth();
		this.playAnimation();
		console.log(this.$router.history.current.path)
		this.imageUrl = 'https://decorsave.co.uk/media/catalog/product/cache/065dc27cee58133323abcb0eca74cc7c/h/o/holden-decor-glasshouse-manyara-navy-teal-wallpaper-90152-2.2811.jpg';
	},

	created() {
		EventBus.$on("image from aboutView", img => {
			console.log(img, 'image from AboutView received!');
			this.imageUrl = img;
		})
	},
};
</script>

<style>
	html, body{
		overflow-y: hidden;
	}

	.menuOpen:hover{
		color: black;
	}

	@keyframes slideIn {
		from {
			opacity: 0;
			transform: translateX(-100%);
		}
		to {
			opacity: 1;
			transform: translateX(0);
		}
	}

	.fade-in {
		/* animation: fadeIn 0.5s ease-in-out; */
		animation: slideIn 1.5s ease;
	}

	.categories{
		color: black;
		background: rgba(230, 230, 230, 0.3); /* Adjust the background color and opacity as needed */
		padding: 20px;
		border-radius: 10px;

		text-decoration: none;
		transition: margin-left 2s;
		transition-timing-function: ease;
		margin: 25px 0 0 25px;
		font-size: 50px;
		font-family: 'Young Serif', serif;
	}
	.categories:hover{
		margin-left: 50px;
		color: black;
		text-decoration: underline;
		font-style: italic;
		
	}
	#particles-js {
		position: relative;
		width: 100%;
		height: 100%;
		/* background-color: #b61924; */
		/* background-image: url("https://mars.nasa.gov/layout/msr/images/home/8-sample-recovery-helicopters.jpg"); */
		background-repeat: no-repeat;
		/* background-size: contain; */
		background-position: 100%;
	}

	/* .unclickable {
		pointer-events: none;
		cursor: not-allowed;
		color: gray;
	} */
	@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,600;1,700;1,800;1,900&family=Young+Serif&display=swap');
</style>