<template>
	<div>
		<h1 id="charsCustom" class="introTitleSmall d-block d-sm-none">trees takes time to grow, so are we.</h1>
		<h1 id="charsCustom" class="introTitle d-none d-sm-block">trees takes time to grow, so are we.</h1>
	</div>
</template>

<script >
import { gsap } from "gsap-trial";
import { ScrambleTextPlugin } from "gsap-trial/ScrambleTextPlugin"
export default {
	data() {
		return {
		}
		
	},

	methods: {
		introAnim(){
			var tl = gsap.timeline({defaults: {duration: 2, ease: "none"}});
			tl.to("#charsCustom", {duration: 3, scrambleText:{text:"trees takes time to grow, so are we.", chars:"こんにちは", revealDelay:1, tweenLength:false, speed:0.4}})
		}
	},

	mounted() {
		gsap.registerPlugin(ScrambleTextPlugin);
		this.introAnim();
	},
}
</script>

